function nextFramePromise(){
	return new Promise((resolve) => {
		requestAnimationFrame(resolve);
	})
}

function stepJacobi(field: GridScalarField, target: GridScalarField): GridScalarField {
	const [w, h] = field.size;
	const t = evaluateDiscrete(target);
	const m = evaluateDiscrete(field);
	const c2 = field.cellSize**2;
	let newValues: number[] = [];
	for (let y = 0; y < w; y++) {
		for (let x = 0; x < h; x++){
			newValues[calculateFlatIndex(w, [x, y])] = (
				c2 * t([x, y]) + m([x - 1, y]) + m([x + 1, y]) + m([x, y - 1]) + m([x, y + 1])
			) / 4;
		}
	}

	return {
		...field,
		values: newValues
	}
}

async function stepJacobiAsync(field: GridScalarField, target: GridScalarField): Promise<GridScalarField> {
	const [w, h] = field.size;
	const t = evaluateDiscrete(target);
	const m = evaluateDiscrete(field);
	const c2 = field.cellSize ** 2;
	let newValues: number[] = [];
	let startTime = performance.now();
	for (let y = 0; y < w; y++) {
		for (let x = 0; x < h; x++) {
			newValues[calculateFlatIndex(w, [x, y])] = (
				c2 * t([x, y]) + m([x - 1, y]) + m([x + 1, y]) + m([x, y - 1]) + m([x, y + 1])
			) / 4;
			const curTime = performance.now();
			if (curTime - startTime > 5){
				await nextFramePromise();
				startTime = performance.now();
			}
		}
	}

	return {
		...field,
		values: newValues
	}
}

const laplacianValueAt = (field: GridScalarField) => ([x, y]: Vector2): number => {
	const m = evaluateDiscrete(field);
	return (4 * m([x, y]) - m([x - 1, y]) - m([x + 1, y]) - m([x, y - 1]) - m([x, y + 1])) / field.cellSize**2;
};
function laplacian(field: GridScalarField): GridScalarField {
	return init(laplacianValueAt(field), field);
}




